body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f7f7f7;
    color: #333;
  }
  
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  /* Navbar Styles */
  nav {
    background-color: #333;
    padding: 10px 20px;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
  }
  
  li {
    margin: 0 10px;
  }
  
  a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  /* General Section Styles */
  .section {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .section-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .section-content {
    margin-bottom: 20px;
  }
  
  /* Home Styles */
  .home-section {
    text-align: center;
    background-image: url("C:/MERN_STACK_INTERN/REACT_JS/task6/src/background.jpg");
    background-size: cover;
    background-position: center;
    color: #fff;
    padding: 80px;
  }
  
  .home-title {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .home-subtitle {
    font-size: 24px;
    font-weight: normal;
    margin-bottom: 40px;
  }
  
  /* About Styles */
  .about-section {
    text-align: center;
  }
  
  .about-content {
    margin-bottom: 40px;
  }
  
  .about-description {
    font-size: 18px;
    line-height: 1.6;
  }
  
  /* Skills Styles */
  .skills-section {
    text-align: center;
  }
  
  .skill-item {
    display: inline-block;
    margin: 10px;
    padding: 10px 20px;
    background-color: #f2f2f2;
    border-radius: 20px;
  }
  
  .skill-item:hover {
    background-color: #e8e8e8;
  }
  
  /* Resume Styles */
  .resume-section {
    text-align: center;
  }
  
  .resume-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .resume-subtitle {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .resume-content {
    margin-bottom: 20px;
  }
  
  .resume-education,
  .resume-experience {
    margin-bottom: 20px;
  }
  
  .resume-education h4,
  .resume-experience h4 {
    margin-bottom: 5px;
  }
  
  .resume-education ul,
  .resume-experience ul {
    margin-bottom: 10px;
  }
  
  .resume-education li,
  .resume-experience li {
    margin-left: 15px;
  }
  
  /* Projects Styles */
  .projects-section {
    text-align: center;
  }
  
  .project-item {
    margin-bottom: 20px;
  }
  
  .project-item h4 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .project-item p {
    margin-bottom: 10px;
  }
  
  /* Contact Styles */
  .contact-section {
    text-align: center;
  }
  
  .contact-content {
    margin-bottom: 40px;
  }
  
  .contact-info {
    margin-bottom: 20px;
  }
  
  .contact-info li {
    margin-bottom: 10px;
  }