.cover-page {
    background-size: cover;
    background-position: center;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .cover-content {
    text-align: center;
    color: #fff;
  }
  
  .cover-title {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .cover-subtitle {
    font-size: 24px;
    margin-bottom: 40px;
  }
 
  .cover-page.with-background {
    background-image: url('background-image.jpg');
    background-size: cover;
    /* Add other background-related styles as needed */
  }

  .cover-title {
    animation: fade-in 1s ease-in-out;
  }
  
  @keyframes fade-in {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .cover-subtitle {
    animation: fade-in 1s ease-in-out 0.5s;
  }