.projects-container {
    margin-top: 20px;
  }
  
  .project-card {
    margin-bottom: 20px;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 5px;
  }
  
  .project-title {
    font-size: 24px;
    margin-bottom: 10px;
    color: #333;
  }
  
  .project-description {
    margin-bottom: 10px;
    color: #666;
  }
  
  .project-technologies {
    margin-bottom: 10px;
    color: #888;
  }
  
  .project-link {
    display: inline-block;
    margin-top: 10px;
    color: #1e90ff;
    text-decoration: none;
  }
  
  .project-link:hover {
    text-decoration: underline;
  }
  
  .project-card:hover {
    box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease-in-out;
  }
  
  @media screen and (max-width: 768px) {
    .projects-container {
      margin-top: 10px;
    }
  
    .project-card {
      padding: 10px;
    }
  
    .project-title {
      font-size: 20px;
      margin-bottom: 8px;
    }
  
    .project-description {
      margin-bottom: 8px;
    }
  
    .project-technologies {
      margin-bottom: 8px;
    }
  }