.skills-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  
  .skill-item {
    margin-bottom: 20px;
    text-align: center;
  }
  
  .progress-bar {
    width: 200px;
    height: 20px;
    background-color: #f1f1f1;
    position: relative;
  }
  
  .progress {
    height: 100%;
    background-color: #4caf50;
    transition: width 0.5s ease;
    position: relative;
  }
  
  .progress-value {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
  }
  
  .skill-item h4 {
    margin-top: 5px;
  }