.contact-container {
    margin: 50px auto;
    max-width: 600px;
    padding: 20px;
    background-color: #f4f4f4;
    border-radius: 10px;
  }
  
  .contact-title {
    text-align: center;
    font-size: 28px;
    margin-bottom: 20px;
  }
  
  .contact-description {
    text-align: center;
    font-size: 16px;
    margin-bottom: 30px;
  }
  
  .contact-details {
    margin-bottom: 30px;
  }
  
  .contact-item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .contact-icon {
    font-size: 20px;
    margin-right: 10px;
  }
  
  .contact-text {
    font-size: 16px;
  }
  
  .social-icons-container {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px;
    overflow: hidden;
    transition: max-width 0.3s ease-in-out;
  }
  
  .social-icons-container.show-icons {
    max-width: 250px;
  }
  
  .social-icons {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
  }
  
  .social-icon {
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
  
  .more-button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background-color: #f4f4f4;
    color: #333;
    font-size: 18px;
    border-radius: 50%;
    cursor: pointer;
    transition: transform 0.3s ease-in-out;
  }
  
  .more-button:hover {
    transform: rotate(180deg);
  }